export default class AppModel {
  constructor(state) {
    this.state = state;
    this.nextPageToken = '';
  }

  static extractClipsInfo(data) {
    return data.items.map(clip => clip);
  }

  async getClipsInfo() {
    const { url } = this.state;
    const responce = await fetch(url);
    const data = await responce.json();
    this.nextPageToken = data.nextPageToken;
    return AppModel.extractClipsInfo(data);
  }

  // eslint-disable-next-line class-methods-use-this
  async getClipsStatistics(statUrl) {
    const responce = await fetch(statUrl);
    const data = await responce.json();
    return AppModel.extractClipsInfo(data);
  }

  getNextPageToken() {
    return this.nextPageToken;
  }
}
