import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      pageOfTwelveElemNumber: 0,
      pageToken: ' ',
      searchValue: ' ',
      url: '',
    };
  }

  async start() {
    const view = new AppView();
    view.render();
    this.controller();
  }

  controller() {
    const inputOne = document.querySelector('.inputOne');

    async function searcher(state) {
      const searchValue = inputOne.value;
      const stateObj = state;
      if (stateObj.searchValue !== searchValue) {
        stateObj.pageOfTwelveElemNumber = 0;
        stateObj.pageToken = '';
      }
      stateObj.searchValue = searchValue;
      stateObj.url = `https://www.googleapis.com/youtube/v3/search?pageToken=${stateObj.pageToken}&part=snippet&maxResults=12&type=video&q=${stateObj.searchValue}&key=AIzaSyA-49FCq9Ez_hSOdQ2pL5aR1pkog6kMmnU`;
      const model = new AppModel(stateObj);
      const data = await model.getClipsInfo();
      const arrForVideoStat = [];
      stateObj.pageToken = model.getNextPageToken();
      for (let i = 0; i < data.length; i += 1) {
        arrForVideoStat.push(data[i].id.videoId);
      }
      const videoForstat = arrForVideoStat.join(',');
      const statUrl = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyA-49FCq9Ez_hSOdQ2pL5aR1pkog6kMmnU&id=${videoForstat},&part=snippet,statistics`;
      const stat = await model.getClipsStatistics(statUrl);
      const view = new AppView(data, stat, stateObj.pageOfTwelveElemNumber);
      await view.renderChange();
    }

    const slider = document.querySelector('.containerForBlock');
    let maxsize = 2000;
    slider.addEventListener('mousedown', () => {
      if (slider.scrollLeft >= maxsize) {
        for (let i = 0; i < 12; i += 1) {
          slider.appendChild(document.createElement('div'));
        }
        this.state.pageOfTwelveElemNumber += 12;
        searcher(this.state);
        maxsize += 2480;
      }
    });
    const sliderSpanFour = document.querySelector('.sliderSpanFour');
    const sliderSpanThree = document.querySelector('.sliderSpanThree');
    const sliderSpanTwo = document.querySelector('.sliderSpanTwo');
    sliderSpanTwo.addEventListener('click', () => {
      const scrollPosition = slider.scrollLeft - slider.clientWidth;
      function move() {
        if (slider.scrollLeft > scrollPosition && slider.scrollLeft > 0) {
          slider.scrollLeft -= slider.clientWidth / 62;
          requestAnimationFrame(move);
          sliderSpanThree.innerHTML = Math.floor(slider.scrollLeft / (slider.clientWidth - 5)) + 1;
        }
      }
      requestAnimationFrame(move);
    });
    sliderSpanFour.addEventListener('click', () => {
      const scrollPosition = slider.scrollLeft + slider.clientWidth;
      function move() {
        if (slider.scrollLeft < scrollPosition) {
          slider.scrollLeft += slider.clientWidth / 60;
          requestAnimationFrame(move);
          sliderSpanThree.innerHTML = Math.floor(slider.scrollLeft / slider.clientWidth) + 1;
        }
      }
      requestAnimationFrame(move);
      if (slider.scrollLeft >= maxsize) {
        for (let i = 0; i < 12; i += 1) {
          slider.appendChild(document.createElement('div'));
        }
        this.state.pageOfTwelveElemNumber += 12;
        searcher(this.state);
        maxsize += 2480;
      }
    });

    inputOne.addEventListener('keyup', searcher.bind(null, this.state));
  }
}
