export default class AppView {
  constructor(data, statistics, pageOfTwelveElemNumber) {
    this.data = data;
    this.statistics = statistics;
    this.pageOfTwelveElemNumber = pageOfTwelveElemNumber;
  }

  render() {
    const fontAwesome = document.createElement('link');
    document.head.appendChild(fontAwesome);
    fontAwesome.rel = 'stylesheet';
    fontAwesome.href = 'https://use.fontawesome.com/releases/v5.8.2/css/all.css';
    fontAwesome.integrity = 'sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay';
    fontAwesome.crossorigin = 'anonymous';

    const mainContainer = document.createElement('div');
    document.body.appendChild(mainContainer);
    mainContainer.className = 'mainContainer';

    const containerForInput = document.createElement('div');
    mainContainer.appendChild(containerForInput);
    containerForInput.className = 'containerForInput';

    const magnifier = document.createElement('span');
    containerForInput.appendChild(magnifier);
    magnifier.innerHTML = '<i class="fas fa-search"></i>';

    const inputOne = document.createElement('input');
    inputOne.className = 'inputOne';
    inputOne.type = 'text';
    inputOne.placeholder = 'Search';
    containerForInput.appendChild(inputOne);

    const containerForBlock = document.createElement('div');
    mainContainer.appendChild(containerForBlock);
    containerForBlock.className = 'containerForBlock';

    const containerForResultOne = document.createElement('div');
    containerForBlock.appendChild(containerForResultOne);
    containerForResultOne.className = 'containerForResultOne';

    const containerForResultTwo = document.createElement('div');
    containerForBlock.appendChild(containerForResultTwo);
    containerForResultTwo.className = 'containerForResultTwo';

    const containerForResultThree = document.createElement('div');
    containerForBlock.appendChild(containerForResultThree);
    containerForResultThree.className = 'containerForResultThree';

    const containerForResultFour = document.createElement('div');
    containerForBlock.appendChild(containerForResultFour);
    containerForResultFour.className = 'containerForResultFour';

    const containerForResultFive = document.createElement('div');
    containerForBlock.appendChild(containerForResultFive);
    containerForResultFive.className = 'containerForResultFive';

    const containerForResultSix = document.createElement('div');
    containerForBlock.appendChild(containerForResultSix);
    containerForResultSix.className = 'containerForResultSix';

    const containerForResultSeven = document.createElement('div');
    containerForBlock.appendChild(containerForResultSeven);
    containerForResultSeven.className = 'containerForResultSeven';

    const containerForResultEight = document.createElement('div');
    containerForBlock.appendChild(containerForResultEight);
    containerForResultEight.className = 'containerForResultEight';

    const containerForResultNine = document.createElement('div');
    containerForBlock.appendChild(containerForResultNine);
    containerForResultNine.className = 'containerForResultNine';

    const containerForResultTen = document.createElement('div');
    containerForBlock.appendChild(containerForResultTen);
    containerForResultTen.className = 'containerForResultTen';

    const containerForResultEleven = document.createElement('div');
    containerForBlock.appendChild(containerForResultEleven);
    containerForResultEleven.className = 'containerForResultEleven';

    const containerForResultTwelve = document.createElement('div');
    containerForBlock.appendChild(containerForResultTwelve);
    containerForResultTwelve.className = 'containerForResultTwelve';

    const slider = document.createElement('div');
    mainContainer.appendChild(slider);
    slider.className = this.titles;
    slider.className = 'slider';

    const sliderSpanOne = document.createElement('span');
    slider.appendChild(sliderSpanOne);
    sliderSpanOne.className = 'sliderSpanOne';

    const sliderSpanTwo = document.createElement('span');
    slider.appendChild(sliderSpanTwo);
    sliderSpanTwo.className = 'sliderSpanTwo';

    const sliderSpanThree = document.createElement('span');
    slider.appendChild(sliderSpanThree);
    sliderSpanThree.className = 'sliderSpanThree';

    const sliderSpanFour = document.createElement('span');
    slider.appendChild(sliderSpanFour);
    sliderSpanFour.className = 'sliderSpanFour';

    AppView.sliderF();
  }

  renderChange() {
    const block = document.querySelectorAll('.containerForBlock>div');
    const arrForVideo = [];
    const { data } = this;
    const { statistics } = this;
    const arrForVideoBlockAll = [];
    for (let i = 0; i < block.length; i += 1) {
      arrForVideoBlockAll.push(block[i]);
    }
    for (let i = 0; i < 12; i += 1) {
      arrForVideo.push(block[i + this.pageOfTwelveElemNumber]);
      if (this.pageOfTwelveElemNumber === 0) {
        arrForVideo[i].innerHTML = '';
      }

      let { title } = data[i].snippet;
      title = title.slice(0, 50);
      const picture = data[i].snippet.thumbnails.high.url;
      const id = data[i].id.videoId;
      const channel = data[i].snippet.channelTitle;
      const publicationDate = data[i].snippet.publishedAt.slice(0, 10);
      const viewRate = statistics[i].statistics.viewCount;
      const description = `${data[i].snippet.description.slice(0, 100)}...`;
      arrForVideoBlockAll[i + this.pageOfTwelveElemNumber].innerHTML += `<div class='imageDiv' style="background-image:url(${picture})"></div>`;
      arrForVideoBlockAll[i + this.pageOfTwelveElemNumber].innerHTML += `<div class='linkDiv'><a href=https://www.youtube.com/watch?v=${id}>${title}</a></div>`;
      arrForVideoBlockAll[i + this.pageOfTwelveElemNumber].innerHTML += `<p><i class="fas fa-user"></i>${channel}</p><p><i class="fa fa-calendar" aria-hidden="true"></i>${publicationDate}</p><p><i class="fas fa-eye"></i>${viewRate}</p><p>${description}</p>`;
    }
  }

  static sliderF() {
    const slider = document.querySelector('.containerForBlock');
    let isDown = false;
    let startX;
    let scrollL;
    const sliderSpanThree = document.querySelector('.sliderSpanThree');

    slider.addEventListener('mousedown', (e) => {
      isDown = true;
      slider.classList.add('active');
      startX = e.pageX - slider.offsetLeft;
      scrollL = slider.scrollLeft;
    });
    slider.addEventListener('mouseleave', () => {
      isDown = false;
      slider.classList.remove('active');
    });
    slider.addEventListener('mouseup', () => {
      isDown = false;
      slider.classList.remove('active');
    });
    slider.addEventListener('mousemove', (e) => {
      if (!isDown) return;
      e.preventDefault();
      const x = e.pageX - slider.offsetLeft;
      const walk = x - startX;
      slider.scrollLeft = scrollL - walk;
      sliderSpanThree.innerHTML = Math.floor(slider.scrollLeft / slider.clientWidth) + 1;
    });

    const sliderSpanOne = document.querySelector('.sliderSpanOne');
    sliderSpanThree.innerHTML = 1;

    sliderSpanOne.addEventListener('click', () => {
      sliderSpanThree.innerHTML = 1;
      const scrollPosition = slider.scrollLeft;
      function move() {
        if (slider.scrollLeft > 0) {
          slider.scrollLeft -= scrollPosition / 60;
          requestAnimationFrame(move);
        }
      }
      requestAnimationFrame(move);
    });
  }
}
